header {
  background-color: #282c34;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  flex-wrap: nowrap;
  padding: 0 16px;
  height: 50px;
  border-bottom: 1px solid #d3d6da;
}

#App {
  height: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.menu-left,
.menu-right {
  display: flex;
  margin: 0;
  padding: 0;
  align-items: center;
  width: 20%;
  justify-content: flex-start;
}

#nav-button {
  padding-top: 2px;
}

button.icon {
  background: none;
  border: none;
  cursor: pointer;
  padding: 0 4px;
}

#range-button {
  margin-left: auto;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.table th {
  vertical-align: middle;
  color: aliceblue;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.title {
  position: relative;
  overflow: hidden;
}
.empty-text {
  width: 23px;
  height: 37px;
}
a span {
  display: inline-block;
  color: aliceblue;
  font-weight: 700;
  font-size: 37px;
  line-height: 100%;
  letter-spacing: 0.01em;
}
a:hover span {
  transition: 2000ms;
  -webkit-transform: rotateX(360deg);
  transform: rotateX(360deg);
}
a span:nth-of-type(4) {
  position: absolute;
  top: -100%;
  left: 28px;
  font-size: 1.9rem;
}
a span:nth-of-type(5) {
  position: absolute;
  top: 5px;
  left: 32px;
}
a:hover span:nth-of-type(1) {
  -webkit-transition-delay: 10ms;
  transition-delay: 10ms;
}
a:hover span:nth-of-type(2) {
  -webkit-transition-delay: 400ms;
  transition-delay: 400ms;
}
a:hover span:nth-of-type(4) {
  top: 10px;
  color: rgb(234, 0, 32);
  -webkit-transition-delay: 800ms;
  transition-delay: 800ms;
}
a:hover span:nth-of-type(5) {
  -webkit-transition-delay: 800ms;
  transition-delay: 800ms;
  top: 100%;
}
a:hover span:nth-of-type(6) {
  -webkit-transition-delay: 1200ms;
  transition-delay: 1200ms;
}
a:hover span:nth-of-type(7) {
  -webkit-transition-delay: 1600ms;
  transition-delay: 1600ms;
}
a:hover span:nth-of-type(8) {
  -webkit-transition-delay: 2000ms;
  transition-delay: 2000ms;
}
